import { Flex, Button, Text } from "@radix-ui/themes";
import React, { useState, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom"; // Import Link from react-router-dom

const NavBar = () => {
  // State to track the screen width
  const [isSmallScreen, setIsSmallScreen] = useState(false);

  // Update the state based on the window width
  useEffect(() => {
    const handleResize = () => {
      setIsSmallScreen(window.innerWidth <= 768); // Set the breakpoint at 768px
    };

    window.addEventListener("resize", handleResize);
    handleResize(); // Call it initially to set the state

    return () => window.removeEventListener("resize", handleResize); // Clean up on unmount
  }, []);

  return (
    <div
      style={{
        position: "sticky",
        top: 0,
        height: "50px",
        width: "100%",
        backgroundColor: "black",
        color: "white",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        zIndex: 1000, // ensures the navbar stays on top of other content
      }}
    >
      <RouterLink to="/" style={{ textDecoration: "none", color: "inherit" }}>
        <div
          style={{
            paddingLeft: "15px",
            fontFamily: "'Bungee Hairline', sans-serif",
            fontWeight: 600,
            fontStyle: "normal",
            cursor: "pointer",
          }}
        >
          <Text size="5">Tellos AI</Text>
        </div>
      </RouterLink>
      <div>
        <Flex gap="6" align="center">
          <RouterLink to="/pricing">
            <Button size="2" variant="ghost" style={{ cursor: "pointer" }}>
              Pricing
            </Button>
          </RouterLink>
          <RouterLink to="/about">
            <Button size="2" variant="ghost" style={{ cursor: "pointer" }}>
              About
            </Button>
          </RouterLink>
        </Flex>
      </div>
      <div style={{ paddingRight: "10px" }}>
        <Flex gap="3" align="center">
          {/* Log In button with inline style */}
          <Button
            size="2"
            variant="ghost"
            style={{
              cursor: "pointer",
              display: isSmallScreen ? "none" : "inline-block", // Hide on small screens
            }}
          >
            Log In
          </Button>
          <Button
            size="2"
            variant="soft"
            color="green"
            style={{ cursor: "pointer" }}
            onClick={() => (window.location.href = "http://localhost:3000")}
          >
            Start learning
          </Button>
        </Flex>
      </div>
    </div>
  );
};

export default NavBar;
